import { Link } from "react-router-dom";

function GlobalHeader() {
  return (
    <>
      <div className="global-header">
        <Link to={"/ep7-database/"}>
          <div className="global-icon"></div>
        </Link>
        <div className="global-item">
          <Link to={"/ep7-database/HeroFilterPage"}>
            <div className="global-hero"></div>
          </Link>
          <Link to={"/ep7-database/ArtifactsFilterPage"}>
            <div className="global-artifacts"></div>
          </Link>
          <div className="global-materials"></div>
        </div>
      </div>
    </>
  );
}

export default GlobalHeader;
